import { useState, useRef } from "react";
import Classes from "./FromNews.module.css";
export const FromNews = () => {
  const [value, setValue] = useState("");
  const [data, setData] = useState([]);
  const [id, setId] = useState(0);
  ////////////////
  const handleChangeInput = (e) => {
    e.preventDefault();
    setValue(e.target.value);
  };
  ////////////////
  const handleSubmit = (value) => {
    const newData = { job: value, id: id };
    setId(id + 1);
    setData((prev) => {
      return [...prev, newData];
    });
    setValue("");
  };
  /////////////////
  const handleBtn = (id) => {
    setData(data.filter((item) => item.id !== id));
  };
  const inputRef = useRef();
  return (
    <div className={Classes.container}>
      <h1>chào mừng đến với todo app</h1>
      <div className={Classes.myInput}>
        <input ref={inputRef} value={value} onChange={handleChangeInput} />
        <button onClick={() => handleSubmit(value)}>add</button>
      </div>
      <div className={Classes.myOutput}>
        <ul>
          {data.map((item) => {
            return (
              <li key={item.id}>
                {item.job}
                <button
                  onClick={() => handleBtn(item.id)}
                  className={Classes.btn}
                >
                  đã xong
                </button>
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};
