import React from "react";
import { useReducer, useRef } from "react";
import "./travle.css";
const A_travel = () => {
  const initialValue = {
    job: "",
    jobs: [],
    id: 0,
  };

  //action start
  const SET_JOB = "set_job";
  const ADD_JOB = "add_job";
  const DEL_JOB = "del_job";
  const INCREASE_ID = "increase_id";
  //action end

  // payload
  function payload_set_job(payload) {
    return {
      type: SET_JOB,
      payload: payload,
    };
  }
  function payload_add_job(payload) {
    return {
      type: ADD_JOB,
      payload: payload,
    };
  }
  function payload_del_job(payload) {
    return {
      type: DEL_JOB,
      payload: payload,
    };
  }

  /// handle start

  const handleSubmit = () => {
    let newjob = job.trim();
    if (newjob != "") {
      dispatch(payload_add_job(newjob));
      dispatch(payload_set_job(""));
      inputRef.current.focus();
    }
  };
  const handleClick = (index) => {
    dispatch(payload_del_job(index));
  };
  const handleInput = (e) => {
    dispatch(payload_set_job(e.target.value));
  };
  // handle end

  // reducer function
  function reducer(state, action) {
    switch (action.type) {
      case SET_JOB:
        return { ...state, job: action.payload };
      case ADD_JOB:
        return { ...state, jobs: [...state.jobs, action.payload] };
      case DEL_JOB:
        let newJob = [...state.jobs];
        newJob.splice(action.payload, 1);

        return {
          ...state,
          jobs: newJob,
        };
      default:
        return new Error("invalid value");
    }
  }
  // reducer function  end
  const [state, dispatch] = useReducer(reducer, initialValue);
  const { job, jobs, id } = state;
  const inputRef = useRef();
  return (
    <>
      <div className="box">
        <h1>đây là todo app</h1>
        <input ref={inputRef} value={job} onChange={handleInput} />
        <button onClick={handleSubmit}>add</button>
      </div>
      <ul className="render">
        {jobs.map((item, index) => {
          return (
            <li key={index}>
              {item}
              <i
                onClick={() => {
                  handleClick(index);
                }}
                class="fa fa-times"
                aria-hidden="true"
              ></i>
            </li>
          );
        })}
      </ul>
    </>
  );
};

export default A_travel;
