import { Route, Switch } from "react-router-dom";

import { Home } from "./pages/Home";
import { News } from "./pages/News";
import { Travel } from "./pages/Travel";
import Games from "./pages/Games";

import NavicationMain from "./component/Layout/Navication/NavicationMain";
function App() {
  return (
    <div className="App">
      <NavicationMain />
      <div style={{ margin: "20px" }}>
        <Switch>
          <Route path="/" exact>
            <Home />
          </Route>
          <Route path="/news">
            <News />
          </Route>
          <Route path="/travel">
            <Travel />
          </Route>
          <Route path="/games">
            <Games />
          </Route>
        </Switch>
      </div>
    </div>
  );
}

export default App;
