  const gift = [
    { id: 1, item: " Patek Philippe" },
    {
      id: 2,
      item: "Rolex",
    },
    {
      id: 3,
      item: "Breguet",
    },
    { id: 4, item: "Blancpain." },
  ];
  const [checked, setChecked] = useState([]);
  const handleInput = (id) => {
    return setChecked((prve) => {
      const isPick = checked.includes(id);
      if (isPick) {
        return checked.filter((item) => {
          return item !== id;
        });
      } else {
        return [...prve, id];
      }
    });
  };
