import { Link } from "react-router-dom";
import React from "react";
import Classes from "./NavicationMain.module.css";

function NavicationMain() {
  return (
    <header>
      <div className={Classes.logo}>
        <Link to="/">
          <h2 className={Classes.iconLogo}>logo</h2>
        </Link>
      </div>
      <nav>
        <ul className={Classes.listNav}>
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="/news">todoApp</Link>
          </li>
          <li>
            <Link to="/travel">du lịch</Link>
          </li>
          <li>
            <Link to="/games">Games</Link>
          </li>
        </ul>
      </nav>
    </header>
  );
}

export default NavicationMain;
